<template>
  <div id="wrapper" @mousemove="mousemove" @mouseup="mouseup">
    <div id="left" :style="newWidth">
      <div class="top-menu">
        <button class="button-horizonal jsoneditor-format margin-s" @click="format">格式化</button>
        <button class="button-horizonal margin-s" @click="compact">压缩</button>
        <span class="margin-s">
          类名:
          <input type="text" class="button-horizonal" v-model="options.className" />
        </span>
        <select name id class="button-horizonal margin-s" v-model="options.mode">
          <option value="ESMapping">ESMapping</option>
          <option value="JSON">普通JSON</option>
        </select>
      </div>
      <div ref="jsonText" class="json-text"></div>
      <!--     <textarea ref="jsonText" name="" class="json-text" cols="30" rows="10"></textarea>-->
    </div>
    <div id="middle" @mousedown="mousedown">
      <button class="button-vertical" @click="genClass">实体类→</button>
    </div>
    <div id="right">
      <div v-for="(item,index) in classes" style="height: 100%" v-bind:key="index">
        <div class="top-menu">
          <button class="button-horizonal margin-s" @click="copy($event,index)">复制</button>
        </div>
        <textarea
          name
          :ref="'resultText'+index"
          class="result-text"
          cols="30"
          rows="10"
          :value="item"
        ></textarea>
      </div>
    </div>
  </div>
</template>
 
<script>
import { def } from '@vue/shared'
import JSONEditor from '../assets/plugins/jsoneditor/jsoneditor.min.js'
export default {
  data: function () {
    return {
      //拖动相关
      dragging: false,
      offsetX: 0,
      lastWidth: 'width:671px',
      newWidth: 'width:671px',
      //jsonEditor实例
      leftJsonEditor: {},
      options: {
        className: 'Demo',
        mode: 'ESMapping',
      },
      beans: [],
      typeMap: {
        binary: { typeClassName: 'String', needImport: false },
        boolean: { typeClassName: 'boolean', needImport: false },
        keyword: { typeClassName: 'String', needImport: false },
        constant_keyword: { typeClassName: 'String', needImport: false },
        wildcard: { typeClassName: 'String', needImport: false },
        short: { typeClassName: 'short', needImport: false },
        byte: { typeClassName: 'byte', needImport: false },
        integer: { typeClassName: 'int', needImport: false },
        long: { typeClassName: 'long', needImport: false },
        double: { typeClassName: 'double', needImport: false },
        date: { typeClassName: 'String', needImport: false },
        object: { typeClassName: '', needImport: false },
        ip: { typeClassName: 'String', needImport: false },
        geo_point: { typeClassName: 'String', needImport: false },
        array: { typeClassName: 'List', needImport: true },
      },
      tpl: '\n    public void setA(T a) {\n        this.a = a;\n    }\n\n    public T getA() {\n        return a;\n    }\n',
      classes: [''],
      index: 0,
    }
  },
  mounted: function () {
    const options = {
      mode: 'code',
      mainMenuBar: false,
      statusBar: false,
      modes: ['code'], // allowed modes
    }
    this.leftJsonEditor = new JSONEditor(this.$refs.jsonText, options, {})
  },
  computed: {},
  methods: {
    copy: function ($event, index) {
      this.$refs['resultText' + index][0].select()
      document.execCommand('Copy')
    },
    mousedown: function ($event) {
      this.dragging = true
      //记录鼠标起始位置
      this.offsetX = $event.pageX
    },
    mousemove: function ($event) {
      if (this.dragging) {
        //计算鼠标移动了多少像素
        var different = $event.pageX - this.offsetX
        //在lastWidth基础上计算是因为在拖动期间，所有位置都应该你基于起始位置而不是不断变换的位置
        this.newWidth =
          'width:' + (parseInt(this.lastWidth.match(/\d+/)) + different) + 'px'
        this.leftJsonEditor.aceEditor.resize()
      }
    },
    mouseup: function ($event) {
      this.dragging = false
      //记录左半边最终宽度
      this.lastWidth = this.newWidth
    },
    /**
     * 生成java实体类
     * */
    genClass: function () {
      this.beans = []
      this.classes = []
      if (this.leftJsonEditor.getText()) {
        try {
          this.leftJsonEditor.get()
        } catch (e) {
          this.classes = [e]
          return
        }
        var jsonObject = JSON.parse(this.leftJsonEditor.getText())
        switch (this.options.mode) {
          case 'JSON':
            this.analyzeJSON(jsonObject, this.options.className)
            break
          default:
            this.analyzeMapping(jsonObject, this.options.className)
            break
        }
        this.beansToClass()
      } else {
        this.classes = ['请输入JSON']
      }
    },
    getType: function (object, field) {
      var result = { typeName: '', imports: '', isPlainObject: false }
      switch (typeof object) {
        case 'string':
          if (isNaN(object) && !isNaN(Date.parse(object))) {
            result.typeName = 'Date'
            result.imports = 'java.util.date'
          } else {
            result.typeName = 'String'
          }
          break
        case 'boolean':
          result.typeName = 'Boolean'
          break
        case 'number':
          result.typeName = 'Integer'
          break
        case 'bigint':
          result.typeName = 'BigDecimal'
          break
        case 'function':
          result.typeName = 'String'
          break
        case 'object':
          if (!object) {
            result.typeName = 'String'
            break
          }
          if (Array.isArray(object)) {
            if (object.length < 1) {
              result.typeName =
                'List' + '<' + this.firstToUpperCase(field) + '>'
            } else {
              result.typeName =
                'List' + '<' + this.getType(object[0], field).typeName + '>'
            }
          } else {
            result.typeName = this.firstToUpperCase(field)
            result.isPlainObject = true
          }
          break
        default:
          result.typeName = 'String'
          break
      }
      return result
    },
    analyzeJSON: function (jsonObject, className) {
      //产生一个新类
      var result = {
        index: this.index++,
        className: className,
        fields: [],
        imports: [],
      }
      //遍历这个属性列表
      for (var field in jsonObject) {
        var type = this.getType(jsonObject[field], field)
        result.fields.push({ name: field, type: type.typeName })
        if (type.isPlainObject) {
          this.analyzeJSON(jsonObject[field], this.firstToUpperCase(field))
        }
        if (type.imports) {
          result.imports.push(type.imports)
        }
      }
      this.beans.push(result)
    },
    analyzeMapping: function (jsonObject, className) {
      //产生一个新类
      var result = {
        index: this.index++,
        className: className,
        fields: [],
        imports: [],
      }
      if (jsonObject.hasOwnProperty('properties')) {
        //遍历这个属性列表
        var properties = jsonObject['properties']
        for (var i in properties) {
          if (properties[i].hasOwnProperty('type')) {
            //基本属性
            result.fields.push({
              name: i,
              type: this.typeMap[properties[i]['type']].typeClassName,
            })
          } else {
            //对象属性
            if (i.endsWith('s')) {
              result.fields.push({
                name: i,
                type:
                  this.typeMap.array.typeClassName +
                  '<' +
                  this.firstToUpperCase(i) +
                  '>',
              })
              this.analyzeMapping(properties[i], this.firstToUpperCase(i))
              if (!result.imports.includes('java.util.List')) {
                result.imports.push('java.util.List')
              }
            } else {
              result.fields.push({ name: i, type: this.firstToUpperCase(i) })
              this.analyzeMapping(properties[i], this.firstToUpperCase(i))
            }
          }
        }
      }
      this.beans.push(result)
    },
    beansToClass: function () {
      var app = this
      this.beans.sort(function (a, b) {
        return a.index - b.index
      })
      this.beans.forEach(function (item) {
        var result = ''
        //imports
        item.imports.forEach(function (item) {
          result = result.concat('import ' + item + ';\n')
        })
        result = result.concat('\n')
        //class name
        result = result.concat('public class ' + item.className)
        result = result.concat('{\n')
        //fields
        item.fields.forEach(function (item) {
          result = result.concat(
            '    public ' + item.type + ' ' + item.name + ';\n'
          )
        })
        //setter and getter

        item.fields.forEach(function (item) {
          var tplMap = {
            a: item.name,
            A: app.firstToUpperCase(item.name),
            T: item.type,
          }
          result = result.concat(
            app.tpl.replace(/a|A|T/g, function (matched) {
              return tplMap[matched]
            })
          )
        })
        //end
        result = result.concat('}')
        app.classes.push(result)
      })
      if (this.beans.length == 0) {
        this.classes = ['']
      }
    },
    /**
     * 格式化
     */
    format: function () {
      try {
        this.leftJsonEditor.format()
      } catch (e) {
        this.$refs.resultText.value = e
      }
    },
    /**
     * 压缩
     */
    compact: function () {
      this.leftJsonEditor.compact()
    },
    firstToUpperCase: function (str) {
      return str.substr(0, 1).toUpperCase() + str.substr(1)
    },
  },
}
</script>
